
import MasterPage from "../components/MasterPage";
import fetch from 'isomorphic-unfetch';
import Precio from "../components/Precio";
import Noticias from "../components/Noticias";


const index = (props) => (
    <MasterPage>
       
        <div className="col-12">
            <h2>Precio del Bitcoin</h2>
            <Precio
                valoresBitcoin={props.valoresBitcoin}
            />
           
        </div>
        <div className="row">
            <div className="col-md-8">
                <h2>Noticias Sobre Bitcoin</h2>
                <Noticias
                    noticiasBitcoin={props.noticiasBitcoin}
                />
            </div>
           
            <div className="col-md-4">
                <h2>Próximos Eventos Bircoin</h2>
                
            </div>
        </div>
    </MasterPage>
);

//Llamamos a la api.
index.getInitialProps = async () =>{
    const precio = await fetch('https://api.coinmarketcap.com/v2/ticker/1/');
    const noticias = await fetch('https://newsapi.org/v2/everything?q=bitcoin&sortBy=publishedAt&apiKey=64b10404173c4dba8496dd6d6a9d053f&language=es');
    //const eventos = await fetch('https://www.eventbriteapi.com/v3/events/search/?q=Bitcoin&sort_by=date&location.address=España?token=YYX2JSQ5GU7WQXCJCOVL');
    const resPrecio = await precio.json();
    const resNoticias = await noticias.json();
    //const resEventos = await eventos.json();
    return {
        valoresBitcoin: resPrecio.data.quotes.USD,
        noticiasBitcoin: resNoticias.articles//,
        //eventosBitcoin: resEventos
    }
}

export default index;